  <section>
    <hr />
    <h1>Suggested Genres</h1>

    <div id="suggestedBookContainer">
      <template v-for="category in $store.state.categories">
        <router-link
          :to="{ name: 'category', params: { name: category.name } }"
          :key="category.id"

        >
          <div class="suggestedCategory">
            <h2>{{ category.name }}</h2>
            <img class="suggestedGenreImage"
              :src="
                require('../assets/images/categories/' +
                  categoryImageFileName(category))
              "
              :alt="category.name"
              style="width:200px;height:150px;"
            />

          </div>
        </router-link>
      </template>
    </div>
    <h1>Recently Purchased</h1>
    <div id="recentlyPurchasedContainer">

      <ul id="suggestedBookList">
        <li class="suggestedBookListItem">
          <a href="#">
            <img src="../assets/images/books/walkingDead.jpg" alt="Walking Dead" style="height:200px;" />
          </a>
        </li>
        <li class="suggestedBookListItem">
          <a href="#">
