<script>

export default {
  name: "HomeCategoryList",
  methods: {
    categoryImageFileName: function(category) {
      let name = category.name.toLowerCase();
      name = name.replace(/ |:/g, "");

      if (name == "art") {
        return `${name}.jpg`;
      }

      return `${name}.png`;
    }
  }
};
</script>

