const url = String(
  window.location.origin.replace(/:\d+$/gi, ":8080") +
    window.location.pathname.substring(
      0,
      window.location.pathname.indexOf("/", 2)
    )
);

export default {
  fetchCategories() {
    console.log("Fetching " + `${url}/api/categories`);
    return fetch(`${url}/api/categories`)
      .then(response => {
        if (response.ok) {
          return response.json();
        }
        throw new Error("Network response was not ok.");
      })
      .catch(reason => {
        console.log("Error fetching category data", reason);
      });
  },
  fetchCategoryBooks(categoryName) {
    console.log(
      "Fetching " + `${url}/api/categories/name/` + categoryName + `/books`
    );
    return fetch(`${url}/api/categories/name/` + categoryName + `/books`)
      .then(response => {
        if (response.ok) {
          return response.json();
        }
        throw new Error("Network response was not ok.");
      })
      .catch(reason => {
        console.log("Error fetching books", reason);
      });
  }
};
